@import url(https://fonts.googleapis.com/css2?family=Mulish:wght@200&display=swap);
body {
  margin: 0;
  padding: 0;
  background-color: #ddf3f5;
  font-family: "Mulish", sans-serif;
}

* {
  box-sizing: border-box;
}

.header {
  background-color: #f2aaaa;
  width: 100%;
  height: 100px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__img {
  width: 80px;
  margin-top: 10px;
  margin-left: 10px;
}

.header__menu {
  margin-right: 60px;
}

.header__menu--profile p {
  color: #000000;
  font-weight: bolder;
  font-size: 18px;
}

.TodoCounter {
  margin: 0;
  font-size: 24px;
  text-align: center;
  padding-top: 50px;
}

.TodoSearch {
  background: #f9f9f9;
  border-radius: 6px;
  border: 2px solid #f9f9f9;
  margin-top: 30px;
  margin-left: 30px;
  height: 64px;
  width: calc(100% - 62px);
  font-size: 24px;
  text-align: center;
  font-family: "Mulish", sans-serif;
  font-weight: 400;
  color: #1e1e1f;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
}

.TodoSearch::-webkit-input-placeholder {
  color: #a5a5a5;
  font-weight: 400;
}

.TodoSearch:-ms-input-placeholder {
  color: #a5a5a5;
  font-weight: 400;
}

.TodoSearch::placeholder {
  color: #a5a5a5;
  font-weight: 400;
}

.TodoSearch:focus {
  outline-color: #e36387;
}

.TodoSearch:hover {
  cursor: pointer;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.TodoItem {
  background-color: #fafafa;
  position: relative;
  display: flex;
  width: 90%;
  justify-content: center;
  align-items: center;
  margin-top: 24px;
  margin-left: 20px;
  box-shadow: 5px 10px 50px rgba(32, 35, 41, 0.15);
  border-radius: 10px;
}

.TodoItem-p {
  margin: 24px 0 24px 24px;
  width: calc(100% - 120px);
  font-size: 18px;
  line-height: 24px;
  font-weight: 400;
}
.TodoItem-p--complete {
  text-decoration: line-through;
}

.Icon {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 28px;
  width: 28px;
  font-size: 24px;
  font-weight: bold;
  /* background-color: #CCC; */
}

.Icon-check {
  position: absolute;
  left: 12px;
}
.Icon-check--active {
  background-color: #4caf50;
}

.Icon-delete {
  position: absolute;
  right: 5px;
}

.LoadingTodo-container {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 24px;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
}

.LoadingTodo-text {
  margin: 24px 0 24px 24px;
  width: calc(100% - 120px);
  font-size: 18px;
  line-height: 24px;
  font-weight: 400;
}

.LoadingTodo-completeIcon,
.LoadingTodo-deleteIcon {
  border-radius: 50px;
  width: 48px;
  height: 48px;
}

.LoadingTodo-completeIcon {
  position: absolute;
  left: 12px;
}

.LoadingTodo-deleteIcon {
  position: absolute;
  top: -24px;
  right: 0;
}

.LoadingTodo-container,
.LoadingTodo-completeIcon,
.LoadingTodo-deleteIcon {
  background: linear-gradient(90deg, rgb(227, 99, 135), rgb(166, 220, 239));
  background-size: 400% 400%;
  -webkit-animation: loadingAnimation 3s ease-in-out infinite;
          animation: loadingAnimation 3s ease-in-out infinite;
}

@-webkit-keyframes loadingAnimation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

@keyframes loadingAnimation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

.Empty-container {
  background-color: #a6dcef;
  width: 100%;
  height: 120px;
  justify-content: center;
  text-align: center;
  border-radius: 10px;
  padding: 10px;
  margin-top: 15px;
}

.empty-text {
  font-size: 28px;
}

.TodoButton {
  background-color: #e36387;
  box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
  border-radius: 50%;
  border: none;
  position: relative;
  cursor: pointer;
  font-size: 50px;
  font-weight: bold;
  color: #ddf3f5;
  display: flex;
  margin-top: 40px;
  margin-left: 40%;
  justify-content: center;
  align-items: center;
  height: 64px;
  width: 64px;
  -webkit-transform: rotate(0);
          transform: rotate(0);
  transition: 0.3s ease;
}

.TodoButton:hover {
  -webkit-transform: rotate(224deg);
          transform: rotate(224deg);
}

.ModalBackground {
  background: rgba(32, 35, 41, 0.8);
  position: fixed;
  top: -10px;
  left: -10px;
  right: -10px;
  bottom: -10px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
}

form {
  width: 90%;
  max-width: 300px;
  padding: 33px 40px;
  display: flex;
  border-radius: 15px;
  box-shadow: 0px 5px 10px rgb(227, 99, 135);
  align-content: center;
  flex-direction: column;
  background-color: #fafafa;
}

label {
  text-align: center;
  font-weight: bold;
  font-size: 20 px;
  color: #000000;
  margin-bottom: 15px;
}

textarea {
  background-color: #fafafa;
  border: 2px, solid #202329;
  border-radius: 10px;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
  color: #000000;
  font-size: 20px;
  text-align: center;
  padding: 12px;
  height: 96px;
  width: calc(100%-25px);
}

textarea::-webkit-input-placeholder {
  color: #a5a5a5;
  font-family: "Mulish", sans-serif;
  font-weight: 400;
}

textarea:-ms-input-placeholder {
  color: #a5a5a5;
  font-family: "Mulish", sans-serif;
  font-weight: 400;
}

textarea::placeholder {
  color: #a5a5a5;
  font-family: "Mulish", sans-serif;
  font-weight: 400;
}

textarea:focus {
  outline-color: #e36387;
}

.TodoForm-buttonContainer {
  margin-top: 14px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.TodoForm-button {
  cursor: pointer;
  display: inline-block;
  font-size: 20px;
  color: #202329;
  font-weight: 400;
  width: 120px;
  height: 48px;
  border-radius: 2px;
  border: none;
  font-family: "Mulish", sans-serif;
}

.TodoForm-button-add {
  background-color: #e36387;
  box-shadow: 0px 5px 25px rgba(32, 35, 41, 0.25);
}

.TodoForm-button-cancel {
  background-color: #fafafa;
}

.footer {
  margin-top: 50%;
  align-items: center;
  background-color: #f2aaaa;
  display: flex;
  height: 100px;
  width: 100%;
}

.footer a {
  color: white;
  cursor: pointer;
  text-decoration: none;
  font-size: 16px;
  padding-left: 30px;
  text-decoration: none;
}

.footer a:hover {
  text-decoration: underline;
}

